interface Allocation {
  optionIndex: number
  selection: "yes" | "no" | null
  amount: number
}

export default function MarketDetailPage() {
  const router = useRouter()
  const params = useParams()
  const marketId = Number.parseInt(params?.id as string)
  
  const [budget, setBudget] = useState<string>("100")
  const [allocations, setAllocations] = useState<Record<number, Allocation>>({})

  const market = markets.find((m) => m.id === marketId)

  useEffect(() => {
    // Initialize allocations for all options
    if (market) {
      const initialAllocations: Record<number, Allocation> = {}
      market.options.forEach((_, idx) => {
        initialAllocations[idx] = {
          optionIndex: idx,
          selection: null,
          amount: 0,
        }
      })
      setAllocations(initialAllocations)
    }
  }, [market])

  if (!market) {
    return (
      <div className="min-h-screen bg-background text-foreground flex items-center justify-center">
        <div className="text-center">
          <p className="text-muted-foreground mb-4">Market not found</p>
          <button
            onClick={() => router.push("/")}
            className="px-4 py-2 rounded-lg bg-foreground text-background hover:opacity-90"
          >
            Go Back
          </button>
        </div>
      </div>
    )
  }

  const parseVolume = (volumeStr: string): number => {
    const match = volumeStr.match(/\$?([\d.]+)([MBK])?/i)
    if (!match) return 0
    const value = parseFloat(match[1])
    const suffix = match[2]?.toUpperCase()
    if (suffix === "B") return value * 1000000000
    if (suffix === "M") return value * 1000000
    if (suffix === "K") return value * 1000
    return value
  }

  const formatCurrency = (value: number): string => {
    if (value >= 1000000000) {
      return `$${(value / 1000000000).toFixed(2)}B`
    } else if (value >= 1000000) {
      return `$${(value / 1000000).toFixed(2)}M`
    } else if (value >= 1000) {
      return `$${(value / 1000).toFixed(2)}K`
    }
    return `$${value.toFixed(2)}`
  }

  const getFormattedVolume = (volumeStr: string): string => {
    const volumeInUSD = parseVolume(volumeStr)
    return formatCurrency(volumeInUSD)
  }

  // Calculate price for Yes/No selection (Polymarket style: Yes + No = 100 cents)
  const calculatePrice = (optionIndex: number, selection: "yes" | "no"): number => {
    const totalPool = market.options.reduce((sum, opt) => sum + opt.amountBet, 0)
    const option = market.options[optionIndex]
    
    // Calculate probability percentage (0-100)
    const probabilityPercent = (option.amountBet / totalPool) * 100
    
    if (selection === "yes") {
      // Yes price in cents = probability percentage
      return probabilityPercent
    } else {
      // No price in cents = 100 - probability percentage
      return 100 - probabilityPercent
    }
  }

  // Convert cents to dollars for display
  const centsToDollars = (cents: number): number => {
    return cents / 100
  }

  // Calculate total allocated amount
  const totalAllocated = Object.values(allocations).reduce(
    (sum, alloc) => sum + (alloc.selection ? alloc.amount : 0),
    0
  )

  const budgetNum = Number.parseFloat(budget) || 0
  const remainingBudget = budgetNum - totalAllocated

  // Handle allocation change
  const handleAllocationChange = (
    optionIndex: number,
    selection: "yes" | "no" | null,
    amount: number
  ) => {
    setAllocations((prev) => {
      const currentAllocation = prev[optionIndex]
      const otherAllocationsTotal = Object.entries(prev)
        .filter(([idx]) => Number(idx) !== optionIndex)
        .reduce((sum, [, alloc]) => sum + (alloc.selection ? alloc.amount : 0), 0)
      
      const maxAllowed = budgetNum - otherAllocationsTotal
      const clampedAmount = Math.max(0, Math.min(amount, maxAllowed))
      
      return {
        ...prev,
        [optionIndex]: {
          optionIndex,
          selection,
          amount: clampedAmount,
        },
      }
    })
  }

  // Calculate potential winnings for an allocation (Polymarket style)
  // If you bet $X at price $P (in dollars), you get $X / $P shares
  // If it wins, each share pays $1, so total return = $X / $P
  const calculatePotentialWinnings = (optionIndex: number, selection: "yes" | "no", amount: number): number => {
    const priceCents = calculatePrice(optionIndex, selection)
    const priceDollars = centsToDollars(priceCents)
    // Total return if it wins (includes original bet)
    return amount / priceDollars
  }

  // Calculate total potential winnings
  const totalPotentialWinnings = Object.values(allocations)
    .filter((alloc) => alloc.selection !== null)
    .reduce((sum, alloc) => {
      if (alloc.selection) {
        return sum + calculatePotentialWinnings(alloc.optionIndex, alloc.selection, alloc.amount)
      }
      return sum
    }, 0)

  return (
    <div className="min-h-screen bg-background text-foreground">
      <AppHeader 
        title="Market Details" 
        showBackButton={true}
        onBackClick={() => router.push("/")}
      />

      {/* Main Content */}
      <main className="max-w-4xl mx-auto px-6 py-8">
        {/* Market Info */}
        <div className="mb-8">
          <div className="flex items-center gap-2 mb-3">
            <span className="text-xs font-medium text-muted-foreground uppercase tracking-widest">
              {market.category}
            </span>
            {market.trend && (
              <div
                className={`flex items-center gap-1 text-xs font-medium ${
                  market.trend > 0
                    ? "text-green-600 dark:text-green-400"
                    : "text-red-600 dark:text-red-400"
                }`}
              >
                <TrendingUp size={12} />
                {market.trend > 0 ? "+" : ""}
                {market.trend}%
              </div>
            )}
          </div>
          <h1 className="text-2xl font-semibold mb-4">{market.question}</h1>
          <div className="flex gap-6 text-sm text-muted-foreground">
            <span>Volume: {getFormattedVolume(market.volume)}</span>
            <span>Traders: {market.traders}</span>
            <span>Closes in: {market.timeLeft}</span>
          </div>
        </div>

        {/* Budget Input */}
        <div className="mb-8 p-6 rounded-lg border border-border bg-muted/30">
          <label className="text-sm font-medium text-muted-foreground uppercase tracking-widest block mb-3">
            Your Budget
          </label>
          <div className="flex items-center gap-2">
            <span className="text-muted-foreground text-lg">$</span>
            <input
              type="number"
              value={budget}
              onChange={(e) => setBudget(e.target.value)}
              placeholder="100"
              className="flex-1 bg-transparent text-foreground text-2xl font-semibold focus:outline-none placeholder-muted-foreground/50"
              step="1"
              min="0"
            />
          </div>
          <div className="mt-4 flex items-center justify-between text-sm">
            <span className="text-muted-foreground">
              Allocated: ${totalAllocated.toFixed(2)}
            </span>
            <span
              className={`font-medium ${
                remainingBudget >= 0 ? "text-green-600 dark:text-green-400" : "text-red-600 dark:text-red-400"
              }`}
            >
              Remaining: ${remainingBudget.toFixed(2)}
            </span>
          </div>
        </div>

        {/* Options with Yes/No Selections */}
        <div className="space-y-6 mb-8">
          <h2 className="text-lg font-semibold mb-4">Allocate Your Budget</h2>
          {market.options.map((option, optionIndex) => {
            const allocation = allocations[optionIndex] || {
              optionIndex,
              selection: null,
              amount: 0,
            }
            const yesPrice = calculatePrice(optionIndex, "yes")
            const noPrice = calculatePrice(optionIndex, "no")
            const totalPool = market.options.reduce((sum, opt) => sum + opt.amountBet, 0)
            const optionPercentage = (option.amountBet / totalPool) * 100

            return (
              <div
                key={optionIndex}
                className="p-6 rounded-lg border border-border bg-muted/20 hover:bg-muted/30 transition-colors"
              >
                {/* Option Header */}
                <div className="flex items-center justify-between mb-4">
                  <div className="flex-1">
                    <h3 className="text-lg font-semibold mb-2">{option.name}</h3>
                    <div className="flex items-center gap-4 text-sm text-muted-foreground">
                      <span>{optionPercentage.toFixed(1)}% probability</span>
                      <span>•</span>
                      <span>{formatCurrency(option.amountBet)} bet</span>
                    </div>
                  </div>
                </div>

                {/* Yes/No Selection Buttons */}
                <div className="grid grid-cols-2 gap-4 mb-4">
                  {/* Yes Button */}
                  <button
                    onClick={() => {
                      if (allocation.selection === "yes") {
                        // Deselect if already selected
                        handleAllocationChange(optionIndex, null, 0)
                      } else {
                        // Switch to Yes, preserve amount if switching from No
                        handleAllocationChange(
                          optionIndex,
                          "yes",
                          allocation.selection === "no" ? allocation.amount : allocation.amount
                        )
                      }
                    }}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      allocation.selection === "yes"
                        ? "border-green-500 bg-green-500/10 text-green-600 dark:text-green-400"
                        : "border-border hover:border-green-500/50 hover:bg-green-500/5"
                    }`}
                  >
                    <div className="text-left">
                      <div className="font-semibold mb-1">Yes</div>
                      <div className="text-xs text-muted-foreground">
                        Price: {yesPrice.toFixed(1)}¢ ({centsToDollars(yesPrice).toFixed(2)})
                      </div>
                    </div>
                  </button>

                  {/* No Button */}
                  <button
                    onClick={() => {
                      if (allocation.selection === "no") {
                        // Deselect if already selected
                        handleAllocationChange(optionIndex, null, 0)
                      } else {
                        // Switch to No, preserve amount if switching from Yes
                        handleAllocationChange(
                          optionIndex,
                          "no",
                          allocation.selection === "yes" ? allocation.amount : allocation.amount
                        )
                      }
                    }}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      allocation.selection === "no"
                        ? "border-red-500 bg-red-500/10 text-red-600 dark:text-red-400"
                        : "border-border hover:border-red-500/50 hover:bg-red-500/5"
                    }`}
                  >
                    <div className="text-left">
                      <div className="font-semibold mb-1">No</div>
                      <div className="text-xs text-muted-foreground">
                        Price: {noPrice.toFixed(1)}¢ ({centsToDollars(noPrice).toFixed(2)})
                      </div>
                    </div>
                  </button>
                </div>

                {/* Amount Input (only show if selection is made) */}
                {allocation.selection && (
                  <div className="mt-4 p-4 rounded-lg bg-background border border-border">
                    <label className="text-xs font-medium text-muted-foreground uppercase tracking-widest block mb-2">
                      Amount to Allocate
                    </label>
                    <div className="flex items-center gap-2 mb-3">
                      <span className="text-muted-foreground">$</span>
                      <input
                        type="number"
                        value={allocation.amount || ""}
                        onChange={(e) => {
                          const amount = Number.parseFloat(e.target.value) || 0
                          handleAllocationChange(optionIndex, allocation.selection, amount)
                        }}
                        placeholder="0.00"
                        className="flex-1 bg-transparent text-foreground font-mono focus:outline-none placeholder-muted-foreground/50"
                        step="0.01"
                        min="0"
                        max={remainingBudget + allocation.amount}
                      />
                    </div>
                    {allocation.amount > 0 && (
                      <div className="text-sm text-muted-foreground">
                        Potential return: ${calculatePotentialWinnings(
                          optionIndex,
                          allocation.selection!,
                          allocation.amount
                        ).toFixed(2)} (includes your ${allocation.amount.toFixed(2)} bet)
                      </div>
                    )}
                    {remainingBudget < 0 && (
                      <div className="text-xs text-red-600 dark:text-red-400 mt-2">
                        Warning: You have exceeded your budget
                      </div>
                    )}
                  </div>
                )}
              </div>
            )
          })}
        </div>

        {/* Summary */}
        {totalAllocated > 0 && (
          <div className="mb-8 p-6 rounded-lg border border-border bg-muted/50">
            <h3 className="text-lg font-semibold mb-4">Allocation Summary</h3>
            <div className="space-y-2 mb-4">
              {Object.values(allocations)
                .filter((alloc) => alloc.selection && alloc.amount > 0)
                .map((alloc, idx) => (
                  <div key={idx} className="flex items-center justify-between text-sm">
                    <span className="text-muted-foreground">
                      {market.options[alloc.optionIndex].name} - {alloc.selection?.toUpperCase()}
                    </span>
                    <span className="font-mono">
                      ${alloc.amount.toFixed(2)}
                    </span>
                  </div>
                ))}
            </div>
            <div className="pt-4 border-t border-border flex items-center justify-between">
              <span className="font-semibold">Total Potential Return:</span>
              <span className="font-mono text-lg font-semibold">
                ${totalPotentialWinnings.toFixed(2)}
              </span>
            </div>
          </div>
        )}

        {/* Action Buttons */}
        <div className="flex gap-3">
          <button
            disabled={totalAllocated === 0 || remainingBudget < 0}
            className={`flex-1 px-6 py-3 rounded-lg font-semibold transition-all duration-200 ${
              totalAllocated > 0 && remainingBudget >= 0
                ? "bg-foreground text-background hover:opacity-90"
                : "bg-muted text-muted-foreground cursor-not-allowed"
            }`}
          >
            Place Allocations
          </button>
          <button
            onClick={() => router.push("/")}
            className="px-6 py-3 text-sm font-medium rounded-lg hover:bg-muted transition-colors border border-border"
          >
            Cancel
          </button>
        </div>
      </main>
    </div>
  )
}
