"use client"

import Link from "next/link"
import { useTheme } from "next-themes"
import { Sun, Moon, Search, Bell, ArrowLeft } from "lucide-react"
import Logo from "@/components/logo"
import AuthButton from "@/components/auth-button"

interface AppHeaderProps {
  title?: string
  showBackButton?: boolean
  onBackClick?: () => void
}

export default function AppHeader({ title = "Markets", showBackButton = false, onBackClick }: AppHeaderProps) {
  const { theme, setTheme, resolvedTheme } = useTheme()

  const toggleTheme = () => {
    const currentTheme = resolvedTheme || theme || "light"
    setTheme(currentTheme === "dark" ? "light" : "dark")
  }

  const isDark = resolvedTheme === "dark" || theme === "dark"

  return (
    <header className="sticky top-0 z-50 border-b border-border bg-background/80 backdrop-blur-md">
      <div className={`mx-auto px-6 py-4 flex items-center justify-between ${showBackButton ? "max-w-4xl" : "max-w-7xl"}`}>
        <div className="flex items-center gap-3">
          {showBackButton && onBackClick && (
            <button
              onClick={onBackClick}
              className="p-2 hover:bg-muted rounded-lg transition-colors"
            >
              <ArrowLeft size={20} />
            </button>
          )}
          <Link href="/" className="flex items-center gap-3 hover:opacity-80 transition-opacity cursor-pointer">
            <Logo variant={5} size={28} />
            <span className="text-base font-semibold tracking-tight">{title}</span>
          </Link>
        </div>

        <div className="flex items-center gap-3">
          <div className="p-2 text-sm font-medium text-muted-foreground">
            $ USD
          </div>

          <button className="p-2 hover:bg-muted rounded-lg transition-all duration-200 text-muted-foreground hover:text-foreground hover:scale-110">
            <Search size={18} />
          </button>
          <button className="p-2 hover:bg-muted rounded-lg transition-all duration-200 text-muted-foreground hover:text-foreground relative hover:scale-110">
            <Bell size={18} />
            <span className="absolute top-1 right-1 w-2 h-2 bg-foreground rounded-full animate-pulse" />
          </button>
          <button
            onClick={toggleTheme}
            className="p-2 hover:bg-muted rounded-lg transition-all duration-200 text-muted-foreground hover:text-foreground hover:scale-110"
          >
            {isDark ? <Sun size={18} /> : <Moon size={18} />}
          </button>
          <AuthButton />
        </div>
      </div>
    </header>
  )
}
